import React, { useState, useEffect } from 'react';
import { Button, Drawer, Typography, List, message } from "antd";
import { checkout, getCart } from "../utils";

const { Text } = Typography;

function MyCart(props) {
    // display/hide cart
    const [ cartVisible, setCartVisible ] = useState(false);
    // set loading
    const [ loading, setLoading ] = useState(false);
    // store cart data
    const [ cartData, setCartData ] = useState();
    // set checking loading
    const [ checking, setChecking ] = useState(false);



    const onOpenDrawer = () => {
        setCartVisible(true);
    }

    const onCloseDrawer = () => {
        setCartVisible(false);
    }

    const onCheckout = () => {
        setChecking(true);
        // inform the server to checkout
        checkout()
            .then( () => {
                message.success(`checkout successfully`);
                setCartVisible(false);
            })
            .catch( err => {
                message.error(err.message);
            })
            .finally( () => {
                setChecking(false);
            })



    }

    useEffect( () => {
        // set loading
        if (cartVisible) {
            setLoading(true);
            // fetch items added in the cart from the server
            getCart()
                .then( response => {
                    setCartData(response);
                })
                .catch( err => {
                    message.error(err.message);
                })
                .finally( () => {
                    setLoading(false);
                })
        }
    }, [cartVisible])

    return (
        <>
            <Button type="primary" shape="round" onClick={onOpenDrawer}>
                Cart
            </Button>
            <Drawer
                title="My Shopping Cart"
                onClose={onCloseDrawer}
                visible={cartVisible}
                width={520}
                footer={
                    <div
                        style={{
                            display: "flex",
                            justifyContent: "space-between",
                        }}
                    >
                        <Text strong={true}>{`Total price: $${cartData?.totalPrice}`}</Text>
                        <div>
                            <Button onClick={onCloseDrawer} style={{ marginRight: 8 }}>
                                Cancel
                            </Button>
                            <Button
                                onClick={onCheckout}
                                type="primary"
                                loading={checking}
                                disabled={loading || cartData?.orderItemList.length === 0}
                            >
                                Checkout
                            </Button>
                        </div>
                    </div>
                }
            >
                <List
                    loading={loading}
                    itemLayout="horizontal"
                    dataSource={cartData?.orderItemList}
                    renderItem={(item) => (
                        <List.Item>
                            <List.Item.Meta
                                title={item.menuItem.name}
                                description={`$${item.price}`}
                            />
                        </List.Item>
                    )}
                />
            </Drawer>
        </>
    );
}

export default MyCart;